<template>
    <yd-layout :class="'unionCot'">
        <yd-navbar title="中国银联发文" bgcolor="#70b6ef" color="#fff" fontsize="0.36rem">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon color="#fff">
                    <slot color="#fff">返回</slot>
                </yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="mainContent handle_main new_label" v-show="mainFlag">
            <div class="mainContentFluid">
                <div class="fluidWidth leaderAdvice">
                    <yd-flexbox direction="vertical">
                        <yd-flexbox-item :id="'scroll_box'">
                            <yd-flexbox>
                                <div class="hint" v-text='subject'></div>
                            </yd-flexbox>
                            <yd-flexbox v-if="huanji != '' || miji != ''">
                                <div class="btn_label">
                                    <span class="jijian" v-if="huanji != ''" v-text="huanji"></span><span class="shangmi" v-if="miji != ''" v-text="miji"></span>
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                    </yd-flexbox>
                    <yd-flexbox direction="vertical" :class="'hasBor'">
                        <yd-flexbox-item :class="'bg_e2'">
                            <ul class="tab_box">
                                <li @click="tab_index = 0" v-bind="{ id : isLeader ? 'js_gaiyao_lead' : 'js_gaiyao' }">
                                    <img src="../../../static/images/label/label_txt.png">
                                    <p>内容概要</p>
                                </li>
                                <li v-bind="{ id : isLeader ? 'js_maintext_lead' : 'js_maintext' }">
                                    <a @click="($route.params.isDo != 1 && maintext == 'javascript:;')  ? textDialog() : (!seePowerFlag) ? maintext = 'javascript:;' : page_jump1(maintext);powerDialog();">
                                        <img v-if="!seePowerFlag" src="../../../static/images/label/label_pdf_shadow.png">
                                        <img v-else src="../../../static/images/label/label_pdf.png">
                                        <p>正 文&nbsp;</p>
                                    </a>
                                </li>
                                <li v-bind="{ id : isLeader ? 'js_file_lead' : 'js_file' }" @click="files.length > 0 && seePowerFlag ? tab_index = 2 : tab_index = 0;powerDialog();">
                                    <img v-if="files.length > 0 && seePowerFlag" src="../../../static/images/label/label_doc.png">
                                    <img v-else src="../../../static/images/label/label_doc_shadow.png">
                                    <p :class='{color9: files.length < 1}'> 附 件</p>
                                </li>
                            </ul>
                        </yd-flexbox-item>
                        <yd-flexbox-item>
                            <div class="tab_content bg_box" :class="{active: !textHidden }" v-if="tab_index == 0">
                                <p v-if="textHidden" class="summary">{{summary.length > 59 ? summary.substring(0, 52) + '...' : summary}}</p>
                                <p v-else class="summary" v-text="summary"></p>
                                <a class="open" v-bind="{ id : isLeader ? (textHidden ? 'js_openall_lead' : 'js_closeall_lead') : (textHidden ? 'js_openall' : 'js_closeall') }" @click="textHidden = !textHidden" v-show="summary != '' && summary.length > 59" href="javascript:;">{{ textHidden ? '展开全部' : '收起概要'}}</a>
                            </div>
                            <div class="tab_content bg_box bg_box2" v-if="tab_index == 2" :class="{active: !fileHidden }">
                                <div class="over">
                                    <div class="range_txt file" v-for='(file, index) in files' @click="queryText(index)" v-text="file"></div>
                                    <a class="open" v-show="files.length > 3" @click="fileHidden = !fileHidden">{{ fileHidden ? '展开附件' : '收起附件'}}</a>
                                </div>
                            </div>
                        </yd-flexbox-item>
                        <yd-flexbox-item>
                            <yd-flexbox>
                                <div class="range">公司领导批示：</div>
                            </yd-flexbox>
                            <yd-flexbox v-if="pishi.length > 0">
                                <div class="opinionOwnBox">
                                    <div class="label_box" v-for="item in pishi">
                                        <div class="range_txt">
                                            <div class="opName fl" v-if="item.userName1 != ''" v-text='item.userName1 + "："'></div>
                                            <div class="fl opNeirong" v-text="item.neirong"></div>
                                        </div>
                                        <div class="range opTime" v-text='getDay(item.submitDate)'></div>
                                    </div>
                                </div>
                            </yd-flexbox>
                            <yd-flexbox v-if="zjyj.length > 0">
                                <div class="opinionOwnBox">
                                    <div class="label_box" v-for="item in zjyj">
                                        <div class="range_txt">
                                            <div class="opName fl" v-if="item.userName1 != ''" v-text='item.userName1 + "："'></div>
                                            <div class="fl opNeirong" v-text="item.neirong"></div>
                                        </div>
                                        <div class="range opTime" v-text='getDay(item.submitDate)'></div>
                                    </div>
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item>
                            <yd-flexbox>
                                <div class="range">办公室核稿意见：</div>
                            </yd-flexbox>
                            <yd-flexbox v-if="bgshepiopinions.length > 0">
                                <div class="opinionOwnBox">
                                    <div class="label_box" v-for="item in bgshepiopinions">
                                        <div class="range_txt">
                                            <div class="opName fl" v-if="item.userName1 != ''" v-text='item.userName1 + "："'></div>
                                            <div class="fl opNeirong" v-text="item.neirong"></div>
                                        </div>
                                        <div class="range opTime" v-text='getDay(item.submitDate)'></div>
                                    </div>
                                </div>
                            </yd-flexbox>
                            <yd-flexbox v-if="bgshegaoopinions.length > 0">
                                <div class="opinionOwnBox">
                                    <div class="label_box" v-for="item in bgshegaoopinions">
                                        <div class="range_txt">
                                            <div class="opName fl" v-if="item.userName1 != ''" v-text='item.userName1 + "："'></div>
                                            <div class="fl opNeirong" v-text="item.neirong"></div>
                                        </div>
                                        <div class="range opTime" v-text='getDay(item.submitDate)'></div>
                                    </div>
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item>
                            <div>
                                <div class="opinion_tab new_tab">
                                    <span v-bind="{ id : isLeader ? 'js_zhuban_lead' : 'js_zhuban' }" @click="opinion_index = 0 " :class="{active: opinion_index == 0}" v-if="part.zhuban != '' " class="zhuban">主办</span>
                                    <span v-bind="{ id : isLeader ? 'js_huiqian_lead' : 'js_huiqian' }" @click="opinion_index = 1 " :class="{active: opinion_index == 1}" v-if="part.huiqianbumen != '' " class="huiqian">会签</span>
                                    <span v-bind="{ id : isLeader ? 'js_fasongfanwei_lead' : 'js_fasongfanwei' }" @click="opinion_index = 2 " :class="{active: opinion_index == 2}" class="huiqian">发送范围</span>
                                    <span v-bind="{ id : isLeader ? 'js_otherinfo_lead' : 'js_otherinfo' }" @click="opinion_index = 3 " :class="{active: opinion_index == 3}" class="huiqian">其他信息</span>
                                </div>
                                <div class="opinion_content" v-if="opinion_index == 0">
                                    <yd-flexbox>
                                        <div class="range otherRange">主办部门：</div>
                                        <div class="range_txt" v-text='part.zhuban'></div>
                                    </yd-flexbox>
                                    <yd-flexbox>
                                        <div class="range otherRange">拟稿人：</div>
                                        <div class="range_txt">{{part.negaoren}}</div>
                                        <a v-for="item in part.phone" :href="`tel:${item}`" class="range_txt blue">{{ "&nbsp;&nbsp;" +item}}</a>
                                    </yd-flexbox>
                                    <yd-flexbox>
                                        <div class="opinionOwnBox">
                                            <div class="label_box" v-for="item in zbbmhepiopinions">
                                                <div class="range_txt">
                                                    <div class="opName fl" v-if="item.userName1 != ''" v-text='item.userName1 + "："'></div>
                                                    <div class="fl opNeirong" v-text="item.neirong"></div>
                                                </div>
                                                <div class="range opTime" v-text='getDay(item.submitDate)'></div>
                                            </div>
                                        </div>
                                    </yd-flexbox>
                                </div>
                                <div class="opinion_content" v-if="opinion_index == 1">
                                    <yd-flexbox>
                                        <div class="range otherRange">会签部门：</div>
                                        <div class="range_txt" v-text='part.huiqianbumen'></div>
                                    </yd-flexbox>
                                    <yd-flexbox>
                                        <div class="opinionOwnBox">
                                            <div class="label_box" v-for="item in huiqianopinions">
                                                <div class="range_txt">
                                                    <div class="opName fl" v-if="item.userName1 != ''" v-text='item.userName1 + "："'></div>
                                                    <div class="fl opNeirong" v-text="item.neirong"></div>
                                                </div>
                                                <div class="range opTime" v-text='getDay(item.submitDate)'></div>
                                            </div>
                                        </div>
                                    </yd-flexbox>
                                </div>
                                <div class="opinion_content" v-if="opinion_index == 2">
                                    <yd-flexbox>
                                        <div class="range otherRange">主送单位：</div>
                                        <div class="range_txt" v-text='zhusong'></div>
                                    </yd-flexbox>
                                    <yd-flexbox>
                                        <div class="range otherRange ">抄报单位：</div>
                                        <div class="range_txt " v-text='chaobao'></div>
                                    </yd-flexbox>
                                    <yd-flexbox>
                                        <div class="range otherRange ">抄送单位：</div>
                                        <div class="range_txt " v-text='chaosong'></div>
                                    </yd-flexbox>
                                    <yd-flexbox>
                                        <div class="range otherRange ">内部发送：</div>
                                        <div class="range_txt " v-text='neifa'></div>
                                    </yd-flexbox>
                                </div>
                                <div class="opinion_content" v-if="opinion_index == 3">
                                    <yd-flexbox>
                                        <div class="range otherRange ">发文号：</div>
                                        <div class="range_txt " v-text='jgdz'></div>
                                    </yd-flexbox>
                                    <yd-flexbox>
                                        <div class="range otherRange ">办文依据：</div>
                                        <div class="yiju_box range_txt">
                                            <div class=" blue" v-for="(yijufile, index) in yijufiles" @click="queryText1(index)" v-text='yijufile'></div>
                                        </div>
                                    </yd-flexbox>
                                    <yd-flexbox>
                                        <div class="range otherRange ">备&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;注：</div>
                                        <div class="range_txt ">
                                            <div class="remarkItem" v-for="remark in remarks" v-text='remark.neirong'></div>
                                        </div>
                                    </yd-flexbox>
                                </div>
                            </div>
                        </yd-flexbox-item>
                    </yd-flexbox>
                </div>
            </div>
        </div>
        <div class="mainContent1" v-show="sendFlag">
            <fill-postscript v-if="shenpiNum > 0 && !branchFlag" v-on:botSetflag="setFlag" :title="subject" :num="shenpiNum"></fill-postscript>
            <branch-Fillpostscript v-if="shenpiNum > 0 && branchFlag" :title="subject" v-on:botSetflag="setFlag" :num="shenpiNum"></branch-Fillpostscript>
        </div>
        <div class="mainContent1" v-show="piyueFlag">
            <assist-approve v-if="liuzhuanNum > 0" v-on:botSetflag="setFlag" :title="subject" :num="liuzhuanNum"></assist-approve>
        </div>
        <yd-tabbar v-if="$route.params.isDo == 0 && mainFlag" class="skip_box" slot="tabbar" bgcolor="#61b3f6" color="#fff" size=".3rem">
            <div class="skip_btn">
                <a id="js_piyue_lead" v-if="approveFlag && toggleFlag" @click="setFlag(1)">
                    <div class="">批&nbsp;&nbsp;&nbsp;&nbsp;阅</div>
                </a>
                <a id="js_piyue_lead" v-else-if="!approveFlag && toggleFlag" @click="setFlag(1)">
                    <div class="">批&nbsp;&nbsp;&nbsp;&nbsp;阅</div>
                </a>
                <a id="js_piyue_lead" v-else-if="approveFlag && !toggleFlag" @click="setFlag(0)">
                    <div class="">批&nbsp;&nbsp;&nbsp;&nbsp;阅</div>
                </a>
                <a id="js_fasong" v-else @click="setFlag(1)">
                    <div class="">发&nbsp;&nbsp;&nbsp;&nbsp;送</div>
                </a>
            </div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
import fillPostscript from './fillPostscript.vue';
import branchFillpostscript from './branchFillPostscript.vue';
import assistApprove from '../assist/assistApprove.vue';
import axios from 'axios';
export default {
    name: 'unionPaySend',
    components: {
        fillPostscript,
        assistApprove,
        branchFillpostscript
    },
    data() {
        return {
            subject: '',
            leader: '',
            huanji: '',
            pishi: '',
            miji: '',
            summary: '',
            textHidden: true,
            fileHidden: true,
            shenpiNum: 0,
            liuzhuanNum: 0,
            zjyj: '',
            fileLength: 0,
            navflag: false,
            huiqian: '',
            qianfa: {},
            part: {},
            liuyan: '',
            files: [],
            chaobao: '',
            chaosong: '',
            neifa: '',
            jgdz: '',
            isLeader: false,
            huiqianopinions: [],
            zbbmhepiopinions: [],
            bgshegaoopinions: [],
            zbbmhegaoopinions: [],
            bgshepiopinions: [], // 办公室主任核批
            kezhixigangwei: '',
            yijufiles: '',
            remarks: '',
            tab_index: 0,
            opinion_index: 0,
            mainFlag: true, // 显示主页面
            sendFlag: false, // 显示审批页面
            piyueFlag: false, // 显示流转页面
            approveFlag: false,
            toggleFlag: false, // 审批按钮跳转送审页面
            file_url: [],
            file_url1: [],
            seePowerFlag: true,
            branchFlag: false, // 是否是分公司
            maintext: 'javascript:;',
            zhusong: '',
            errorTime: 0
        }
    },
    mounted() {
        let vm = this;
        vm.pageRender();
        vm.setTitleName('中国银联发文');
        document.setTitle('中国银联发文');
        var setNavigationBarTitle = function(title) {
            UPCHAT.M.NAPI.setNavigationBarTitle(title);
        };
        setTimeout(function() {
            var title = '中国银联发文';
            if (UPCHAT.M.isNative()) {
                UPCHAT.M.init(setNavigationBarTitle(title));
            }
        }, 200);
        vm.errorTime = 0;
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo',
            handleInfo: 'getHandleInfo'
        }),
        ...mapGetters(['getNewsInfo'])
    },
    methods: {
        ...mapMutations([
            'setHandleInfo', 'setTitleName'
        ]),
        pageRender() {
            var vm = this;
            if (Number(vm.userInfo.role) == 2) {
                vm.isLeader = true;
            }
            // 判断是否是分公司签报的发送页面
            vm.userInfo.deptname.indexOf('分公司') == -1 ? vm.branchFlag = false : vm.branchFlag = true;
            vm.setHandleInfo({ activityDefId: vm.$route.params.activeId, keepAlive: [], doApprove: '' });
            var scrollobj = document.querySelector('#scrollView');
            var scrollH = document.querySelector('#scroll_box').offsetHeight;
            scrollobj.onscroll = function() {
                var ht = scrollobj.scrollTop;
                if (ht > scrollH) {
                    vm.navflag = true;
                } else {
                    vm.navflag = false;
                }
            };
            if (vm.handleInfo.keepData != '' && vm.handleInfo.keepData.workId == vm.$route.params.workItemID) {
                vm.Render(vm.handleInfo.keepData);
                vm.file_url = vm.handleInfo.file_url;
                vm.file_url1 = vm.handleInfo.file_url1;
                if (vm.handleInfo.mainText == '') {
                    vm.loadText();
                } else {
                    vm.maintext = vm.handleInfo.mainText;
                }
                if (vm.file_url.length < vm.files.length && vm.files.length > 0) {
                    vm.loadFile();
                }
                if (vm.file_url1.length < vm.yijufiles.length && vm.yijufiles.length > 0) {
                    vm.loadFile1();
                }
            } else {
                vm.$dialog.loading.open('加载中...');
                const promise1 = new Promise(function(resolve, reject) {
                    vm.getM(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                        'type': vm.$route.params.type,
                        'workItemID': vm.$route.params.workItemID,
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
                    }, function(data1) {
                        if (data1.isSuccess == 'false') {
                            vm.errorTime++;
                            if (vm.errorTime < 3) {
                                vm.pageRender();
                            } else {
                                vm.$dialog.loading.close();
                                vm.$dialog.toast({
                                    mes: '表单未加载成功',
                                    timeout: 2000,
                                    callback: () => {
                                        vm.$router.push('/handle');
                                    }
                                });
                            }
                        } else if (data1.isSuccess == 'true') {
                            vm.$dialog.loading.close();
                        }
                        if (data1.jsonData.CURRENTSTATE == 10 || vm.$route.params.isDo == 1) {
                            vm.Render(data1.jsonData);
                            resolve(true);
                        } else if (data1.jsonData.CURRENTSTATE != 10 && vm.$route.params.isDo != 1 && data1.isSuccess == 'true') {
                            vm.$dialog.toast({
                                mes: '该流程已被处理',
                                timeout: 2000,
                                callback: () => {
                                    setTimeout(function() {
                                        vm.$router.push('/handle');
                                    })
                                }
                            });
                        }
                    }, function(res) {
                        if (res.response.status != 200) {
                            vm.errorTime++;
                            if (vm.errorTime < 3) {
                                vm.pageRender();
                                console.log(vm.errorTime);
                            } else {
                                vm.$dialog.loading.close();
                                vm.$dialog.toast({
                                    mes: '表单未加载成功',
                                    timeout: 2000,
                                    callback: () => {
                                        vm.$router.push('/handle');
                                    }
                                });
                            }
                        }
                    });
                });
                promise1.then(function(data) {
                    vm.loadText();
                    vm.loadFile();
                    vm.loadFile1();
                });
            }
        },
        loadText() {
            const vm = this;
            // 阅读正文附件接口
            axios.get('/portalMgmt/oa/menus/getPdf', {
                params: {
                    'type': vm.$route.params.type,
                    'resultType': 'queryDocByWorkItemIDResult',
                    'workItemID': vm.$route.params.workItemID,
                    'workTitle': vm.subject,
                    'random': Math.floor(Math.random() * (100000 + 1))
                }
            }).then(function(data4) {
                vm.fillname = data4.data.jsonData.fileName;
                // vm.maintext = vm.conf.readWord + '?workItemId=' + vm.$route.params.workItemID + '&isiOS=' + vm.isIos;
                vm.maintext = data4.data.jsonData == null ? 'javascript:;' : data4.data.jsonData.pdfUrl;
                vm.setHandleInfo({ mainText: vm.maintext });
            });
        },
        loadFile(fileArr = this.files) {
            let vm = this;
            vm.files.forEach(function(item, index) {
                if (item.indexOf('.rar') != -1) {
                    vm.file_url[index] = '.rar';
                    vm.setHandleInfo({ file_url: vm.file_url });
                } else if (item.indexOf('.zip') != -1) {
                    vm.file_url[index] = '.zip';
                    vm.setHandleInfo({ file_url: vm.file_url });
                } else {
                    vm.$http.get('/portalMgmt/oa/menus/getPdf', {
                        params: {
                            'type': vm.$route.params.type,
                            'fileName': item,
                            'workTitle': vm.subject,
                            'workItemID': vm.$route.params.workItemID,
                            'resultType': 'queryAttachByWorkItemIDResult2',
                            'random': Math.floor(Math.random() * (100000 + 1))
                        }
                    }).then((res) => {
                        vm.file_url[index] = res.data.jsonData;
                        vm.setHandleInfo({ file_url: vm.file_url });
                    }).catch(error => {
                        // vm.$dialog.toast({
                        //     mes: '系统正在开小差，请稍后再试',
                        //     timeout: 1500
                        // });
                        console.log(error);
                        console.log(error);
                    });
                }
            });
        },
        loadFile1(fileArr = this.yijufiles) {
            let vm = this;
            vm.yijufiles.forEach(function(item, index) {
                if (item.indexOf('.rar') != -1) {
                    vm.file_url1[index] = '.rar';
                    vm.setHandleInfo({ file_url1: vm.file_url1, mainText: '4' });
                } else if (item.indexOf('.zip') != -1) {
                    vm.file_url[index] = '.zip';
                    vm.setHandleInfo({ file_url: vm.file_url });
                } else {
                    vm.$http.get('/portalMgmt/oa/menus/getPdf', {
                        params: {
                            'type': vm.$route.params.type,
                            'fileName': item,
                            'workTitle': vm.subject,
                            'workItemID': vm.$route.params.workItemID,
                            'resultType': 'queryAttachByWorkItemIDResult2',
                            'random': Math.floor(Math.random() * (100000 + 1))
                        }
                    }).then((res) => {
                        vm.file_url1[index] = res.data.jsonData;
                        vm.setHandleInfo({ file_url1: vm.file_url1 });
                    }).catch(error => {
                        // vm.$dialog.toast({
                        //     mes: '系统正在开小差，请稍后再试',
                        //     timeout: 1500
                        // });
                        console.log(error);
                    });
                }
            });
        },
        Render(data1) {
            let vm = this;
            // 判断是否已经处理
            if (data1.CurrectPersonID != undefined && data1.CurrectPersonID != vm.userInfo.userid) {
                vm.$dialog.toast({
                    mes: '该流程已处理',
                    timeout: 1000,
                    callback: () => {
                        setTimeout(function() {
                            vm.$router.push('/handle');
                        }, 3000)
                    }
                });
                return false;
            }
            // 详情
            function detail1(data) {
                vm.subject = data.subject;
                vm.miji = data.miji;
                vm.huanji = data.huanji;
                vm.leader = data.zusonglingdao;
                if (data.lingdaoopinions == null || data.lingdaoopinions == []) {
                    vm.pishi = '';
                } else {
                    vm.pishi = data.lingdaoopinions;
                }
                if (data.zjgwopinions == null || data.zjgwopinions == []) {
                    vm.zjyj = '';
                } else {
                    vm.zjyj = data.zjgwopinions;
                }
                vm.zhusong = data.zhusong;
                vm.chaobao = data.chaobao;
                vm.chaosong = data.chaosong;
                vm.neifa = data.neifa;
                vm.jgdz = data.bianhao;
                vm.summary = data.summary;
                vm.part.zhuban = data.zhubanbumen; // 主办部门
                vm.qianfa = data.zhubanopinions;
                vm.huiqianopinions = data.huiqianopinions; // 会签意见
                vm.zbbmhepiopinions = data.zbbmhepiopinions; // 主办部门负责人核批：
                vm.bgshegaoopinions = data.bgshegaoopinions; // 办公室核稿
                vm.zbbmhegaoopinions = data.zbbmhegaoopinions; // 主办部门负责人核稿：
                vm.kezhixigangwei = data.kezhixigangwei; // 可知悉的岗位或级别：
                vm.bgshepiopinions = data.bgshepiopinions;
                vm.yijufiles = data.yijufiles; // 办文依据
                vm.remarks = data.remarks; // 备注
                vm.part.negaoren = data.negaoren;
                vm.part.huiqianbumen = data.huiqianbumen == '' ? '' : data.huiqianbumen;
                vm.part.yijufiles = data.yijufiles;
                vm.part.remarks = data.remarks;
                vm.part.phone = data.negaorentel.split('/');
                vm.files = data.files;
                if (data.files.length > 1) {
                    vm.fileLength = 1;
                }
                // 发送审批判断
                for (let key in data.nextBtn) {
                    if (key == 'doApprove') {
                        if (data.nextBtn.doApprove.type == '2') {
                            vm.approveFlag = true;
                        }
                    } else if (key == 'doFinishInner') {
                        // 当 nextBtn 为doFinishInner  时，是部内处理 环节；页面 为审批页面
                        vm.approveFlag = true;
                    } else {
                        if (key == 'zgsSecretaryAppActivity') {
                            vm.approveFlag = true;
                        }
                    }
                }
                data.workId = vm.$route.params.workItemID;
                // 领导
                vm.setHandleInfo({ nextBtn: data.nextBtn, keepData: data });
                vm.seePower();
            }
            detail1(data1);
            if (vm.$route.params.activeId == 'fwZjgwApproval' || vm.$route.params.activeId == 'fwZgsBossApproval' || vm.$route.params.activeId == 'fwFgsBossApproval') {
                vm.approveFlag = true;
            }
            if (vm.$route.params.activeId == 'fwFawenhegaoAct') {
                vm.toggleFlag = true;
                vm.setHandleInfo({ doApprove: 'toggle' });
                vm.setHandleInfo({ activityDefId: 'fwFawenhegaoAct' });
            }
        },
        // 正文
        page_jump1(url, isImg = 'notImg') {
            const vm = this;
            if (vm.$route.params.isDO != 1) {
                var urls = vm.isIOS == true ? url : '/pdfjs/web/viewer.html?file=' + url;
                window.location.href = urls;
            }
        },
        page_jump(detailUrl, isImg = 'notImg') {
            const vm = this;
            if (vm.$route.params.isDO != 1) {
                if (detailUrl.fileType != '.pdf') {
                    let index1 = detailUrl.pdfUrl.lastIndexOf('.') + 1;
                    let index2 = detailUrl.pdfUrl.length;
                    let txtName = detailUrl.pdfUrl.substring(index1, index2);
                    if (txtName != 'pdf') {
                        window.location.href = detailUrl.pdfUrl;
                    } else {
                        var urls = vm.isIOS == true ? (detailUrl.pdfUrl.substring(0, detailUrl.pdfUrl.lastIndexOf('.')) + detailUrl.fileType) : '/pdfjs/web/viewer.html?file=' + detailUrl.pdfUrl;
                        window.location.href = urls;
                    }
                    // var urls = vm.isIOS == true ? (detailUrl.pdfUrl.substring(0, detailUrl.pdfUrl.lastIndexOf('.')) + detailUrl.fileType) : '/pdfjs/web/viewer.html?file=' + detailUrl.pdfUrl;
                    // window.location.href = urls;
                } else {
                    var urls2 = vm.isIOS == true ? detailUrl.pdfUrl : '/pdfjs/web/viewer.html?file=' + detailUrl.pdfUrl;
                    window.location.href = urls2;
                }
            }
        },
        textDialog() {
            this.$dialog.toast({
                mes: '正在加载中,请稍后再点',
                timeout: 1000
            });
        },
        getDay(date) {
            date = date.replace(/-/g, '/');
            return (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '日';
        },
        queryText(index, urlArr = this.file_url) {
            var vm = this;
            let detailUrl = urlArr[index];
            if (!detailUrl) {
                vm.$dialog.toast({
                    mes: '附件较大，正在加载中，请稍候',
                    timeout: 1500
                });
            } else if (detailUrl == '.rar') {
                vm.$dialog.toast({
                    mes: '目前暂不支持rar文件查看',
                    timeout: 2000
                });
            } else if (detailUrl == '.zip') {
                vm.$dialog.toast({
                    mes: '目前暂不支持zip文件查看',
                    timeout: 2000
                });
            } else {
                vm.page_jump(detailUrl);
            }
        },
        queryText1(index, urlArr = this.file_url1) {
            var vm = this;
            let detailUrl = urlArr[index];
            if (!detailUrl) {
                vm.$dialog.toast({
                    mes: '附件较大，正在加载中，请稍候',
                    timeout: 1500
                });
            } else if (detailUrl == '.rar') {
                vm.$dialog.toast({
                    mes: '移动平台暂不支持rar文件展示',
                    timeout: 1000
                });
            } else {
                if (detailUrl.fileType != '.png' && detailUrl.fileType != '.jpg') {
                    vm.page_jump(detailUrl.pdfUrl);
                } else {
                    window.location.href = detailUrl.pdfUrl;
                }
            }
        },
        // 已办列表需要按权限查看附件和正文
        seePower() {
            const vm = this;
            // 对于没有“密级”的流程，给秘书（当前环节事务助理）和公司领导 可以查看正文和附件；
            if (vm.$route.params.isDo == 1) {
                console.log(vm.miji)
                if (vm.miji != '') {
                    vm.seePowerFlag = false;
                    console.log(1)
                } else {
                    console.log(2)
                    if (vm.handleInfo.activityDefId == 'zgsSecretaryAppActivity' || vm.handleInfo.activityDefId == 'fwZgsSecretaryApproval' || vm.handleInfo.activityDefId == 'zgs_ldmsqf_activity' || vm.userInfo.role == 2) {
                        vm.seePowerFlag = true;
                    } else {
                        vm.seePowerFlag = false;
                    }
                }
            }
        },
        // 按权限查看附件和正文提示
        powerDialog() {
            const vm = this;
            if (!vm.seePowerFlag) {
                vm.$dialog.toast({
                    mes: '由于安全保密要求，已办文件暂不支持正文和附件查看！',
                    timeout: 1500
                });
            }
        },
        setFlag(type) {
            // 0流转日志 1是审批日志
            if (type == 0) {
                this.piyueFlag = true;
                this.sendFlag = false;
                this.mainFlag = false;
                this.liuzhuanNum++;
            } else if (type == 1) {
                this.sendFlag = true;
                this.mainFlag = false;
                this.piyueFlag = false;
                this.shenpiNum++;
            } else {
                this.mainFlag = true;
                this.sendFlag = false;
                this.piyueFlag = false;
            }
        }
    }

}
</script>
<style scoped>
.yd-tabbar-item {
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
}

.remarkItem {
    margin-bottom: 0.2rem;
    padding-right: 0.15rem;
}

.unionCot {
    background: #fff;
}

.yd-tabbar .yd-tabbar-item:first-child {
    border-right: 1px solid #cacaca;
}

.yd-tabbar-txt {
    font-size: 0.3rem;
}

.handle_main .otherRange {
    padding-right: 0.2rem;
    white-space: nowrap;
}

.scrollView {
    background: #fff;
}

.yiju_box {
    overflow: hidden;
}

.yiju_box .blue {}

.fluidWidth {
    padding-top: 0;
}

.new_tab {
    overflow: visible !important;
}

.mainContentFluid {
    margin-top: 0;
}

.workingHours {
    padding-left: 0.15rem;
}

.leader {
    width: 100%;
}

.leader .attachment,
.leader .issueName {
    margin-left: 0;
}

.yd-scrollview {
    background: #fff;
}

.blue {
    padding-top: 0.03rem;
}

.range_txt {
    min-height: 0.28rem;
    text-align: justify;
}

.range {
    white-space: nowrap;
}

.comein {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;
}

.leader .issueName {
    text-align: right;
    padding-right: 3rem;
}

.attachment {
    color: #1381dd;
    max-width: 7.5rem;
    float: left;
    margin: 0;
    width: auto;
    margin-bottom: 0.1rem;
}

.technique {
    color: #1381dd;
    font-weight: bold;
    max-width: 7.5rem;
}

.leaderAdvice .yd-flexbox {
    position: relative;
}

.label_box {
    position: relative;
}

.issueTime {
    float: right;
    margin-top: 0;
}

.issueName {
    margin-left: 4rem;
    margin-top: 0;
    color: #0a0a0a;
    margin-bottom: 0.1rem;
    height: 0.3rem;
    line-height: 0.3rem;
}
</style>


